.container {
    max-width: 1200px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    padding-left: 20px;
    padding-right: 20px;
    box-sizing: border-box;
    /* Remove this line: overflow: hidden; */
  }
  
  .full-width-image {
    width: 100%;
    margin-top: 80px; 
  }
  
  .full-width-image img {
    width: 100%;
    height: auto;
    display: block;
  }