.case-study-testimonial {
  background-color: #E6F0FF;
  padding: 40px 40px;
  margin: 80px 0;
  border-radius: 20px;
}

.testimonial-content {
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
}

blockquote {
  font-size: 1.25rem;
  line-height: 1.6;
  color: #333;
  margin-bottom: 40px;
  font-style: normal;
}

.testimonial-author {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.author-avatar {
  width: 64px;
  height: 64px;
  border-radius: 50%;
  margin-bottom: 20px;
}

.author-info {
  text-align: center;
}

.author-name {
  font-weight: 500;
  font-size: 1.1rem;
  margin-bottom: 5px;
}

.author-title,
.author-company {
  font-size: 1rem;
  color: #666;
  margin: 0;
}

@media (max-width: 768px) {
  .case-study-testimonial {
    padding: 60px 20px;
  }

  blockquote {
    font-size: 1.1rem;
  }
}
